export const shareKakao = (route, title) => { // url id    route  
    if (window.Kakao) {
      const kakao = window.Kakao;
      if (!kakao.isInitialized()) {
        kakao.init("a75d16e7ea132d7152ce13b003c6cc24"); //   javascript key  -> .env 
      }
  
      kakao.Link.sendDefault({
        objectType: "feed", //     type  feed  ->    
        content: {
          title: title, //   title
          description: "2024.11.24   1 20 ", //   title
          imageUrl: "https://ifh.cc/g/sAwh6Z.jpg",
          link: {
            mobileWebUrl: route, //   route(uri )
            webUrl: route
          }
        },
        buttons: [
          {
            title: "  ",
            link: {
              mobileWebUrl: route,
              webUrl: route
            }
          }
        ]
      });
    }
  };