import React from "react";
import "./accountModal.scss";
import {CopyToClipboard} from "react-copy-to-clipboard/src";

const AccountModal = (props) => {
  const closeAccountModal = () => {
    if (props.closeAccountModal) props.closeAccountModal();
  };
  return (
    props.visible && (
      <div className="accountModal">
        <div className="accountModal-inner">
          <div className="accountModal-inner-inner">
            <div className="accountModal-inner-box">
            {props.index === 0 && <div className="accountModal-inner-box">
              <div>
                <div className="accountModal-inner-box-header">
                  <div> </div>
                  <CopyToClipboard className="accountModal-inner-box-header-copy" text="068120823448" onCopy={()=>alert(" ")}>
                <text></text></CopyToClipboard>
                </div>
                <div className="accountModal-inner-box-body">
                  <div>(: )</div>
                  <div>068120823448</div>
                </div>
              </div>
              <div>
                <div className="accountModal-inner-box-header">
                  <div> </div>
                  <CopyToClipboard className="accountModal-inner-box-header-copy" text="94810101092334" onCopy={()=>alert(" ")}>
                <text></text></CopyToClipboard>
                </div>
                <div className="accountModal-inner-box-body">
                  <div>(: )</div>
                  <div>94810101092334</div>
                </div>
              </div>
              <div>
                <div className="accountModal-inner-box-header">
                  <div> </div>
                  <CopyToClipboard className="accountModal-inner-box-header-copy" text="273120320273" onCopy={()=>alert(" ")}>
                <text></text></CopyToClipboard>
                </div>
                <div className="accountModal-inner-box-body">
                  <div>(: )</div>
                  <div>273120320273</div>
                </div>
              </div>
            </div>}
            {props.index === 1 && <div className="accountModal-inner-box">
              <div>
                <div className="accountModal-inner-box-header">
                  <div> </div>
                  <CopyToClipboard className="accountModal-inner-box-header-copy" text="02480104459984" onCopy={()=>alert(" ")}>
                <text></text></CopyToClipboard>
                </div>
                <div className="accountModal-inner-box-body">
                  <div>(: )</div>
                  <div>02480104459984</div>
                </div>
              </div>
            </div>}
            <div className="accountModal-inner-button" onClick={closeAccountModal}>
              
            </div>
          </div>
        </div>
      </div>
      </div>
    )
  );
};

export default AccountModal;
